<template>
  <div class="staticReports todoProcess loadingtext">
    <el-container>
      <el-header>
        <div class="staticReports minWidth">
          <div class="taticReports-content">
            <el-row type="flex" class=" staticReports-header " justify="space-between">
              <el-col :span="17" class="titleMT"><span class="mz" @click="$router.back(-1)">流程报表</span><i :class="icon"></i>
              </el-col>
              <el-col :span="6" class="titleBtn clearfix">
                <el-button round @click="refresh" type="primary" size="mini">重置</el-button>
                <el-button round @click="search" type="primary" size="mini">搜索</el-button>
              </el-col>
            </el-row>
          </div>
        </div>
      </el-header>

      <el-main>
        <div class="mainContaint">
          <div class="todoProcess-screenList screenList-box" :class="{screenList_box_ie:isIE}">
            <el-row>
              <el-col :span="24" class="report">
                <span >未结束流程列表</span>
              </el-col>
            </el-row>

            <!-- 更多筛选 begin -->
            <el-form  label-width="100px" size="mini" class="todoForm" v-model="processform" ref="processform">
              <!-- 第一行  begin  -->
              <el-row type="">
                <!-- 公司 组织 项目 -->
                <CascaderOrganize :isShowStar="true" :defaultFirst="defaultFirst" ref="cascaderOrganize" @changeSetvaule="changeSetvaule"></CascaderOrganize>
              </el-row>
              <!-- 第二行  begin  -->
              <el-row type="">
                <el-col :span="8">
                  <el-form-item label="发起时段">
                    <span style="color: red; font-size: 14px; float: left; position: absolute; top: 0; left: -10px;">*</span>
                    <div class="money-box">
                      <el-form-item>
                        <el-date-picker
                          class="todoDate"
                          v-model="processform.date[0]"
                          value-format="yyyy-MM-dd"
                          type="date"
                          :clearable="false"
                          :picker-options="pickerOptionsBefore"
                          @focus="hideDate"
                          placeholder="选择日期">
                        </el-date-picker>
                      </el-form-item>
                      <el-form-item>~</el-form-item>
                      <el-form-item>
                        <el-date-picker
                          class="todoDate"
                          style="float: right;"
                          v-model="processform.date[1]"
                          value-format="yyyy-MM-dd"
                          type="date"
                          :clearable="false"
                          :picker-options="pickerOptionsAfter"
                          @focus="hideDate"
                          placeholder="选择日期">
                        </el-date-picker>
                      </el-form-item>
                    </div>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label="流程分类">
                    <span @dblclick="openDailog('processClassification')">
                      <el-input placeholder="请选择..." disabled  v-model="processCategoryName"  class="input-text" :title="processCategoryName"></el-input>
                    </span>
                    <i class="iconfont icon-category "  @click="openDailog('processClassification')"></i>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label="流程">
                    <span @dblclick="openDailog('processSelection')">
                      <el-input placeholder="请选择..."  disabled  v-model="processName"  class="input-text" :title="processName"></el-input>
                    </span>
                    <i class="iconfont icon-category "  @click="openDailog('processSelection')"></i>
                  </el-form-item>
                </el-col>

              </el-row>
              <el-row>
                <el-col :span="8">
                  <el-form-item label="审批超过">
                    <span style="color: red; font-size: 14px; float: left; position: absolute; z-index: 99; top: 0; left: -10px;">*</span>
                     <span class="clearfix">
                      <el-input  type="text" v-model="processform.numDay"
                      class="input-embedded input-text" v-on:input="inputValidate(processform.numDay)"></el-input>
                    <i class=" text-embedded">天未结束</i>
                    </span>
                  </el-form-item>
                </el-col>
              </el-row>

              <!-- 第二行  end  -->
            </el-form>
            <!-- 更多筛选 end -->

            <div class="todoProcess-main">

              <!--筛选列表 按钮列 begin-->
              <el-row type="flex" justify="end" class="listButton">
                <el-button round @click="exportList"  class="upload" type="primary" icon="iconfont icon-export" size="mini">导出
                </el-button>
              </el-row>
               <!--筛选列表 按钮列 end-->

              <div class="list" :class="{table_safari:isSafari}">
                <el-table
                  @sort-change="sortChange"
                  :height="bodyHeight - 400"
                  :data="tableData"
                  :header-row-class-name="tableHeaderClassName"
                  ref="singleTable"
                  border
                  highlight-current-row
                  size="mini">

                  <el-table-column
                    sortable='custom'
                    property="processInstanceID"
                    label="流程ID"
                    width="100px">
                  </el-table-column>

                  <el-table-column
                    sortable='custom'
                    width="120px"
                    property="categoryFullName"
                    align="left"
                    label="流程类别">
                    <template slot-scope="scope">
                      <div class="table-popover" :title="scope.row.categoryFullName">{{ scope.row.categoryFullName }}</div>
                    </template>
                  </el-table-column>

                  <el-table-column
                    width="80px"
                    sortable='custom'
                    property="processName"
                    label="流程">
                    <template slot-scope="scope">
                      <div class="table-popover" :title="scope.row.processName">{{ scope.row.processName }}</div>
                    </template>
                  </el-table-column>

                  <el-table-column
                    min-width="120px"
                    sortable='custom'
                    property="folio"
                    label="流程主题">
                    <template slot-scope="scope">
                      <div class="table-popover folio" @click="jumpToOld(scope.row)" :title="scope.row.folio">{{ scope.row.folio }}</div>
                    </template>
                  </el-table-column>

                  <el-table-column
                    width="120px"
                    sortable='custom'
                    property="departmentName"
                    label="部门名称">
                    <template slot-scope="scope">
                      <div class="table-popover" :title="scope.row.departmentName">{{ scope.row.departmentName }}</div>
                    </template>
                  </el-table-column>

                  <el-table-column
                    width="100px"
                    sortable='custom'
                    property="projectName"
                    label="项目">
                    <template slot-scope="scope">
                      <div class="table-popover" :title="scope.row.projectName">{{ scope.row.projectName }}</div>
                    </template>
                  </el-table-column>

                  <el-table-column
                    width="100px"
                    sortable='custom'
                    property="originator"
                    label="发起人">
                    <template slot-scope="scope">
                      <div class="table-popover" :title="scope.row.originator">{{ scope.row.originator }}</div>
                    </template>
                  </el-table-column>

                  <el-table-column
                    width="110px"
                    sortable='custom'
                    prop="startDate"
                    label="发起时间">
                    <template slot-scope="scope">
                      <div class="table-popover" :title="scope.row.startDate | moment">{{scope.row.startDate | moment}}</div>
                    </template>
                  </el-table-column>

                  <el-table-column
                    sortable='custom'
                    property="activityName"
                    width="130px"
                    label="审批步骤名称">
                    <template slot-scope="scope">
                      <div class="table-popover" :title="scope.row.activityName">{{ scope.row.activityName }}</div>
                    </template>
                  </el-table-column>

                  <el-table-column
                    sortable='custom'
                    property="curDest"
                    width="120px"
                    label="当前处理人">
                    <template slot-scope="scope">
                      <div class="table-popover" :title="scope.row.curDest">{{ scope.row.curDest }}</div>
                    </template>
                  </el-table-column>

                  <el-table-column
                    sortable='custom'
                    property="curDepartment"
                    label="当前处理人所属部门"
                    width="180px">
                    <template slot-scope="scope">
                      <div class="table-popover" :title="scope.row.curDepartment">{{ scope.row.curDepartment }}</div>
                    </template>
                  </el-table-column>

                  <el-table-column
                    property="lastApprovedDate"
                    sortable='custom'
                    width="130px"
                    label="上次审批时间">
                    <template slot-scope="scope">
                      <div class="table-popover" :title="scope.row.lastApprovedDate">{{ scope.row.lastApprovedDate }}</div>
                    </template>
                  </el-table-column>

                  <el-table-column
                    property="lastTime"
                    width="120px"
                    sortable='custom'
                    label="持续时间">
                    <template slot-scope="scope">
                      <div class="table-popover" :title="scope.row.lastTime">{{ scope.row.lastTime }}</div>
                    </template>
                  </el-table-column>

                  <div slot="empty">
                    <p>暂无数据</p>
                  </div>
                  <div slot="append">
                    <!-- 分页 -->
                    <page v-if="tableData.length > 0" :pageIndex="pageIndex"
                          :total="total"
                          :pageSize="pageSize"
                          :pageSizesList="pageSizesList"
                          @handleSizeChange="handleSizeChange"
                          @handleCurrentChange="handleCurrentChange">
                    </page>
                  </div>
                </el-table>

              </div>

            </div>
          </div>
        </div>
          <!--弹窗组件-->
          <component :is="componentParam.componentType" v-if="showdialog"
                     @hideComponent="hideComponent" v-on:sureSubmit="showMsgfromchild"
                     v-bind:componentParam="componentParam"></component>
      </el-main>
    </el-container>

  </div>
</template>

<script>
  import { mapState, mapGetters } from "vuex";
  import page from "../plugins/page/index";
  import loading from "../plugins/loading/loading";
  import moment from 'moment'
  import util from '../js/util.js';
  import CascaderOrganize from "../plugins/cascaderOrganize/index";
  import processSelection from '../components/dailog/processSelection.vue'
  import processClassification from '../components/dailog/processClassification.vue'

  export default {
    name: "notFinished",
    components: {
      CascaderOrganize,
      'processClassification': processClassification, //选择流程分类
      'processSelection':processSelection,            //选择流程
      'page':page
    },
    data(){
      return{
        //日期禁用
        pickerOptionsBefore:{
          disabledDate: (time) => {
            let beginDateVal = this.processform.date[1];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") > beginDateVal;
            }
          }
        },
        pickerOptionsAfter:{
          disabledDate: (time) => {
            let beginDateVal = this.processform.date[0];
            if (beginDateVal) {
              return moment(time).format("YYYY-MM-DD") < beginDateVal;
            }
          }
        },
        icon: 'el-icon-arrow-down',
        loading: false, //加载loading
        processFormName: "", // 流程类别名称
        processCategoryName:'',//流程分类名
        processCategoryIDs:'',//流程分类id
        processName:'',// 流程名
        processIDs:'',//流程id
        //表单数据
        defaultFirst:['organize'],
        processform: {
          organize: {}, //当前选中 公司
          department: {}, //当前选中 部门
          projects: {}, //当前选中 项目
          //时间
          date: [
            moment()
              .subtract(1, "months")
              .format("YYYY-MM-DD"),
            moment().format("YYYY-MM-DD")
          ],
          numDay:'',//未结束的天数
          processCategoryList: [], //已选择 流程分类 数据
          processList: [] //流程 数据
        },
        isShow: true,    // 报表条件标题内容是否展开收起
        arrt: '收起',    // 报表条件提示字
        // 查询条件
        model : {
          approveTime:"",
          url:"127.0.0.1",//主题页面url
          port:"3306",//主题页面port
          companyID:"",//公司Id
          startDateFrom:"",//开始时间
          startDateTo:"",//结束时间
          departmentID:"",//部门Id
          projectTD:"",//项目Id
          processID:"",//流程Id
          processCategoryIDs: "", //流程分类Id
          beforeTheDate:""  //未结束的天数
        },
        tableData:[],//表格数据
        oneCheck:false,
        disabled: false,//禁用
        childDataUrl: {url: '',selectedPerson:[]},//传给子组件的值
        showdialog: false,//动态变更弹出框组件
        //component 弹窗 子组件 所有参数
        componentParam:{
          //弹窗类型
          componentType:{
            type: String,
            required: true
          },
          //需要 回填到组件上的 数据集合
          backfillData:{
            type: Array,
            required: true
          },
          //子组件是否单选(true单选，false不是单选)
          oneCheck:{
            type: Boolean,
            required: true
          },
          //控制弹窗头部标题显示隐藏(默认显示)
          closeShow:{
            type: Boolean,
            required: true
          },
        },
        pageIndex: 1, // 分页参数
        total: 0,
        pageSize: 20,
        pageSizesList: [20,50,100],
      }
    },
    mounted() {
    },
    /**
     * 未结束流程报表界面构造函数
     */
    methods: {
      // 主题跳转旧系统
      jumpToOld (row) {
        if(util.browserIsIE()){
          //用静态页面接收发起和审批页面
          var url=escape(row.viewUrl);
          window.open("oldIframe.html?"+"viewurl="+url, "_blank");
        }else{
          window.open(row.viewUrl);
        }
      },
      //表格排序
      sortChange(column) {
        let custom = column.prop == "statusName" ? "dodoType" : column.prop;
        this.model.orderBy = `${custom} ${column.order.replace("ending", "")}`;
        this.pageIndex = 1;
        this.getResultList();
      },
      // 点击input 不显示控件 点击icon图标显示控件
      hideDate(val) {
        setTimeout( () => {
          let dateNodeStart = undefined;
          let dateNodeEnd = undefined;
          let startBool = null;
          let endBool = null;
          if (document.getElementsByClassName('el-picker-panel')[0]) {
            startBool = true;
          }
          if (document.getElementsByClassName('el-picker-panel')[1]) {
            endBool = true;
          }

          dateNodeStart = startBool ? document.getElementsByClassName('el-picker-panel')[0] : '';
          dateNodeEnd = endBool ? document.getElementsByClassName('el-picker-panel')[1] : '';

          if (val.$el.firstElementChild == document.activeElement) {
            startBool ? dateNodeStart.style.display = 'none' : '';
            endBool ? dateNodeEnd.style.display = 'none' : ''
          } else {
            startBool ? dateNodeStart.style.display = 'block' : '';
            endBool ? dateNodeEnd.style.display = 'block' : ''
          }
        }, 10)
      },
      //翻页
      handleSizeChange(val) {
        var pageSize = `${val}`;
        this.pageIndex = 1;
        this.pageSize = parseInt(pageSize);
        localStorage.myInitPageSize = val;
        this.getResultList();
      },
      handleCurrentChange(val) {
        this.pageIndex = val;
        this.getResultList();
      },

      //公司、组织、项目 控件 选择后触发
      changeSetvaule(val) {
        this.processform = Object.assign({},this.processform,val.data)
      },
      /**
       *设置表头标题class
       */
      tableHeaderClassName({row, rowIndex}) {
        return "table-head-th";
      },
      /**
       * 打开弹框
       */
      openDailog(type) {
        //控制弹窗头部标题显示隐藏(默认显示)
        this.componentParam.closeShow = false;
        // 弹出框类型
        this.componentParam.componentType = type;
        //是否单选
        this.componentParam.oneCheck = false;
        //设置回填数据
        if(type == 'processClassification'){
          this.componentParam.backfillData = this.processform.processCategoryList;
        }else  if(type == 'processSelection'){
          this.componentParam.backfillData = this.processform.processList;
        }
        this.showdialog = true;
        util.showDailog_mask();//解决ie浏览器问题显示遮罩
      },
      /**
       * 关闭弹出框组件
       */
      hideComponent() {
        this.showdialog = false;
        util.hiddenDailog_mask();//解决ie浏览器问题 隐藏遮罩
      },
      /**
       * 弹窗组件带出数据赋值给主组件
       */
      showMsgfromchild(data) {
        //流程分类 选择
        if (data.type && data.type == "processClassification") {
          this.processform.processCategoryList = data.list;
          this.handleProcessCategory()
        } else if (data.type && data.type == "processSelection") {
          //流程 选择
          this.processform.processList = data.list;
          this.handleProcess()
        }
        this.hideComponent();
      },

      /**
       * 搜索
       */
      search() {
          this.pageIndex = 1;
          this.getResultList();
      },
      /**
       * reset
       */
      refresh() {
        this.processform={
          organize: {}, //当前选中 公司
          department: {}, //当前选中 部门
          projects: {}, //当前选中 项目
          //时间
          date: [moment().subtract(1, "months").format("YYYY-MM-DD"),moment().format("YYYY-MM-DD")],
          processCategoryList: [], //已选择 流程分类 数据
          processList: [], //流程 数据
          numDay:''
        }
        this.$refs.cascaderOrganize.$emit('refresh') // 刷新 公司、部门、项目组件
      },
      /**
       * 导出
       */
      exportList(){
         let that = this;
        if(this.modelValidate())return   //数据校验
        loading.startLoading({
          lock: true,
          text: '加载中...',
          background: 'rgba(255, 255, 255, 0.9)',
          target: document.querySelector('.loadingtext')
        });
        that.$axios.post('/api/exportExcelForTable',  {'tableName':'未结束流程列表','params': that.model}, {responseType: 'blob'}).then((result) => {
          const content = result.data;
          const dowLoadFileName = '未结束流程列表';
          util.exportSearchList(dowLoadFileName, content);
          loading.endLoading();
        }).catch((err) => {
          loading.endLoading();
          that.$message.error(`导出Excel失败!ERROR MESSAGE:${err}`);
        });
      },
      // 验证 正整数
      inputValidate(value) {
        let that = this;
        let regPos = /^([1-9]\d{0,9}|0)([.]?|(\.\d{1,2})?)$/; //验证金额正则
        if (!regPos.test(value)) {
          setTimeout(function() {
            that.processform.numDay = "";
          }, 100);
        }
      },
      modelValidate(){
        if(this.model.processID == '' && this.model.processCategoryIDs == ''){
          this.$message.warning('请选择流程分类或流程！')
          return true
        }else if(this.model.beforeTheDate==''){
          this.$message.warning(`超过N天结束，不能为空！`);
          return true
        }else if(this.model.beforeTheDate>10000){
          this.$message.warning(`超过N天结束，必须为小于10000的数字。`)
          return true
        }
        return false
      },
      /**
       * 获取报表结果请求数据
       */
      getResultList(){
        let that = this;
        console.log(this.model.companyID);
        if (this.model.companyID === '') {
          this.$message.warning('请选择所属公司');
          return;
        }
        if(this.modelValidate())return;   //数据校验
        if(!this.model.startDateFrom || !this.model.startDateTo){
          this.$message.warning('请选择日期');
          return;
        }
        if (that.model.startDateFrom !== '' || that.model.startDateTo !== '') {
          if(that.model.startDateFrom < '1753-01-01' || that.model.startDateTo < '1753-01-01') {
            this.$message.warning('请选择1753-01-01 ~ 9999-12-31之间的日期');
            // this.processform.date = [];
            return;
          }
        }
        loading.startLoading();
        this.$axios.post('/api/UnfinishProcess/page?pageIndex='
          +(this.pageIndex-1)+'&pageSize='+this.pageSize+'',this.model).then( (res) => {
          loading.endLoading();
            if(res.status===200){
            this.tableData = res.data.unfinishProcessList;
            this.total = res.data.pageControllerInfo.totalRecordCount;
          }else {
              loading.endLoading();
              this.$message.error("请求失败！");

          }
        }).catch(err => {
          loading.endLoading();
          this.$message.error(`查询列表数据失败!ERROR MESSAGE:${err}`);
        })

      },
      //处理 流程数据
      handleProcess(){
        let processId = []
        let processName = []
        for (let node of this.processform.processList) {
          processName.push(node.name)
          processId.push(node.processId + "")
        }
        this.processName = processName.join("；")
        this.processIDs = processId.join(",")
        return processId.join(",")||''
      },
      //处理 流程分类数据
      handleProcessCategory(){
        let processCategoryIDs = []
        let processCategoryName = []
        for (let node of this.processform.processCategoryList) {
            processCategoryName.push(node.name)
            processCategoryIDs.push(node.id + "")
        }
        this.processCategoryName = processCategoryName.join("；")
        this.processCategoryIDs = processCategoryIDs.join(",")
        return processCategoryIDs.join(",")||''
      },
      //处理查询条件数据
      handleQueryParam(data){
        this.model = {
          approveTime:"",
          url: "127.0.0.1", //主题页面url
          port: "3306", //主题页面port
          companyID: data.organize.orgID||'', //公司Id
          startDateFrom: data.date[0], //开始时间
          startDateTo: data.date[1], //结束时间
          departmentID:data.department.id||'', //部门Id
          projectID: data.projects.pc_ID||'', //项目Id
          processCategoryIDs: this.handleProcessCategory(), //流程分类Id
          processID: this.handleProcess() ,//流程Id
          beforeTheDate:data.numDay    //未结束的天数
        }
      }
    },
    watch:{
      processform:{
        handler(val, old) {
          this.handleQueryParam(val)
        },
        deep: true
      }
    },
    computed: {
      //监听表格的高度
      bodyHeight() {
        return document.documentElement.clientHeight;
      },
      ...mapGetters(["isIE", "isSafari", "bodyHeight"])
    }
  }
</script>

<style lang="scss" scoped>

</style>





